/*----------------------
	2. Header Area
------------------------*/
.logo a {display: inline-block}
#main-menu {
    left: 0;
    position: absolute;
    right: 0;
    top: 95px;
    z-index: 999999;
}
#main-menu.stick {
    background: #2d3e50 none repeat scroll 0 0;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
    padding: 25px 0;
    /* position: fixed; */
    top: 0;
}
.main-menu {display: inline-block; margin: 8px 0;}
.main-menu nav ul li {display: inline-block}
.main-menu nav ul li a {
    color: #ffffff;
    display: block;
    font-family: "Raleway",sans-serif;
    font-size: 12px;
    font-weight: 800;
    line-height: 26px;
    margin-right: 34px;
    padding: 0 1px;
    position: relative;
    text-transform: uppercase;
}
.main-menu nav ul li a:before {
    background: #7A9757 none repeat scroll 0 0;
    bottom: 0;
    content: "";
    height: 2px;
    left: 50%;
    position: absolute;
    right: 50%;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-property: left, right;
    transition-property: left, right;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
    z-index: -1;
}
.main-menu nav ul li a:hover:before, .main-menu nav ul li.active a:before {
    left: 0;
    right: 0;
}
.main-menu nav ul li:last-child a {margin-right: 0}
.main-menu nav ul li.active a, .main-menu nav ul li a:hover {color: #fff}
header .menu-btn {
    float: right;
    font-size: 11px;
    line-height: 27px;
    margin-top: 8px;
    padding: 0 16px;
}
